<template>
  <div>
    <div class="col-md-6 offset-md-3 mt-5">
      <a target="_blank" href="https://getform.io?ref=codeSandboxVue">
        <img src="https://imgur.com/Z2Nyxsm.png" width="125" />
      </a>
      <br />
      <a
        target="_blank"
        href="https://getform.io?ref=codeSandboxVue"
        class="mt-3 d-flex"
        >Get your free endpoint now</a
      >
      <h1>Getform.io Vue Form Example</h1>

      <form
        accept-charset="UTF-8"
        v-on:submit.prevent="onSubmit()"
        method="POST"
        enctype="multipart/form-data"
        id="ajaxForm"
      >
        <div class="form-group">
          <label for="exampleInputEmail1" required="required"
            >Email address</label
          >
          <input
            type="email"
            v-model="email"
            class="form-control"
            id="exampleInputEmail1"
            aria-describedby="emailHelp"
            placeholder="Enter email"
          />
        </div>
        <div class="form-group">
          <label for="exampleInputName">Name</label>
          <input
            type="text"
            v-model="name"
            class="form-control"
            id="exampleInputName"
            placeholder="Enter your name"
            required="required"
          />
        </div>
        <div class="form-group">
          <label for="exampleFormControlSelect1">Favourite Platform</label>
          <select
            class="form-control"
            id="exampleFormControlSelect1"
            v-model="platform"
            required="required"
          >
            <option>Github</option>
            <option>Gitlab</option>
            <option>Bitbucket</option>
          </select>
        </div>
        <hr />
        <div class="form-group mt-3">
          <label class="mr-2">Upload your CV:</label>
          <input type="file" @change="processFile($event)" />
        </div>
        <hr />
        <div class="success" v-if="isSuccess">
          We received your submission, thank you!
        </div>
        <button type="submit" class="btn btn-primary">Submit</button>
      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "HelloWorld",
  props: {
    msg: String,
  },
  data() {
    return {
      message: "",
      loading: true,
      name: "",
      email: "",
      files: [],
      platform: "",
      isSuccess: false,
    };
  },
  methods: {
    processFile(event) {
      this.files = event.target.files[0];
    },
    onSubmit() {
      this.message = "";
      const formData = new FormData();
      formData.append("file", this.files);
      formData.append("name", this.name);
      formData.append("this.platform", this.platform);
      formData.append("email", this.email);

      axios
        .post(
          "https://getform.io/f/b22f10be-75a6-40c0-9ec6-3519dc38fe29",
          formData,
          {
            headers: {
              Accept: "application/json",
            },
          }
        )
        .then(
          (response) => {
            this.isSuccess = response.data.success ? true : false;
          },
          (response) => {
            // Error
          }
        );
    },
  },
};
</script>

